.square {
  width: 100%;
  aspect-ratio: 1/1;
  border-top: 1px solid var(--border-color); /* border color defined in the SquareContainer styles */
  border-left: 1px solid var(--border-color);
  transition: background-color 100ms ease-out;

  &_filled {
    background-color: #4c76da;
  }
}